import { isLinkHttp } from '@vuepress/helper/client';
export const resolveRepoLink = (link, provider) => {
    if (!link || isLinkHttp(link))
        return link;
    if (provider === 'github')
        return `https://github.com/${link}`;
    if (provider === 'gitee')
        return `https://gitee.com/${link}`;
    return link;
};
